import "./Photo.css";

import {useEffect, useState} from 'react';
import {useParams, Link} from "react-router-dom";

function Photo({getPhoto}) {
  // –ü–æ–ª—É—á–∞–µ–º —Ç–æ –∑–Ω–∞—á–µ–Ω–∏–µ, –∫–æ—Ç–æ—Ä–æ–µ –º—ã –ø–µ—Ä–µ–¥–∞–ª–∏ –≤ –∫–æ–º—Ä–æ–Ω–µ–Ω—Ç Link –∞—Ç—Ä–∏–±—É—Ç—É to
  const {id} = useParams();
  // –∏—Å–ø–æ–ª—å–∑—É–µ–º –¥–µ—Å—Ç—Ä—É–∫—Ç—É—Ä–∏–∑–∞—Ü–∏—é, –∏–∑–≤–ª–µ–∫–∞–µ–º –∑–Ω–∞—á–µ–Ω–∏—è –∏–∑ –ø–æ–ª—É—á–µ–Ω–Ω–æ–≥–æ –æ–±—ä–µ–∫—Ç–∞
  const {title, subtitle, src} = getPhoto(id);

  return (
    <div className="photo">
      <Link className="photo-goback" to="/">‚üµ Go back</Link>
      <img className="photo-image" src={src} alt={title} />
      <p className="photo-title">{title}</p>
      <p className="photo-subtitle">{subtitle}</p>
    </div>
  )
}

export default Photo;